'use client';

import { useState, useEffect } from 'react';
import { getWalletConnect, getWallet } from '@/lib/sdk';
import { appConfig } from '@/lib/config';

interface WalletConnectProps {
  onConnect?: (address: string) => void;
  onDisconnect?: () => void;
}

export default function WalletConnect({ onConnect, onDisconnect }: WalletConnectProps) {
  const [isConnected, setIsConnected] = useState(false);
  const [address, setAddress] = useState<string | null>(null);
  const [balance, setBalance] = useState<string>('0');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Check connection on mount
  useEffect(() => {
    checkConnection();
  }, []);

  // Update balance when connected
  useEffect(() => {
    if (isConnected && address) {
      updateBalance();
      const interval = setInterval(updateBalance, 10000); // Update every 10s
      return () => clearInterval(interval);
    }
  }, [isConnected, address]);

  async function checkConnection() {
    try {
      const wc = await getWalletConnect();
      const connected = wc.isWalletConnected();
      if (connected) {
        const addr = wc.getConnectedAddress();
        if (addr) {
          setIsConnected(true);
          setAddress(addr);
          onConnect?.(addr);
        }
      }
    } catch (err) {
      console.error('Connection check error:', err);
    }
  }

  async function updateBalance() {
    if (!address) return;
    
    try {
      const w = await getWallet();
      const bal = await w.getBalance(address, appConfig.asaId);
      setBalance(bal.formatted || '0');
    } catch (err) {
      console.error('Balance update error:', err);
    }
  }

  async function handleConnect() {
    setLoading(true);
    setError(null);
    
    try {
      const wc = await getWalletConnect();
      // Initialize Pera Wallet
      await wc.initPeraWallet();
      
      // Connect wallet
      const addr = await wc.connectWallet();
      
      if (addr) {
        setIsConnected(true);
        setAddress(addr);
        await updateBalance();
        onConnect?.(addr);
      }
    } catch (err: any) {
      setError(err.message || 'Failed to connect wallet');
      console.error('Wallet connection error:', err);
    } finally {
      setLoading(false);
    }
  }

  async function handleDisconnect() {
    setLoading(true);
    setError(null);
    
    try {
      const wc = await getWalletConnect();
      await wc.disconnectWallet();
      setIsConnected(false);
      setAddress(null);
      setBalance('0');
      onDisconnect?.();
    } catch (err: any) {
      setError(err.message || 'Failed to disconnect wallet');
      console.error('Wallet disconnection error:', err);
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="flex flex-col gap-4">
      {error && (
        <div className="rounded-lg bg-red-50 p-3 text-sm text-red-600 dark:bg-red-900/20 dark:text-red-400">
          {error}
        </div>
      )}
      
      {isConnected && address ? (
        <div className="flex flex-col gap-2 rounded-lg border border-zinc-200 bg-white p-4 dark:border-zinc-800 dark:bg-zinc-900">
          <div className="flex items-center justify-between">
            <div className="flex flex-col">
              <span className="text-sm text-zinc-600 dark:text-zinc-400">Connected</span>
              <span className="font-mono text-sm text-zinc-900 dark:text-zinc-100">
                {address.slice(0, 6)}...{address.slice(-4)}
              </span>
            </div>
            <div className="flex flex-col items-end">
              <span className="text-sm text-zinc-600 dark:text-zinc-400">Balance</span>
              <span className="font-semibold text-zinc-900 dark:text-zinc-100">
                {balance} PLY
              </span>
            </div>
          </div>
          <button
            onClick={handleDisconnect}
            disabled={loading}
            className="mt-2 rounded-lg bg-red-500 px-4 py-2 text-sm font-medium text-white transition-colors hover:bg-red-600 disabled:opacity-50"
          >
            {loading ? 'Disconnecting...' : 'Disconnect'}
          </button>
        </div>
      ) : (
        <button
          onClick={handleConnect}
          disabled={loading}
          className="rounded-lg bg-blue-500 px-6 py-3 text-sm font-medium text-white transition-colors hover:bg-blue-600 disabled:opacity-50"
        >
          {loading ? 'Connecting...' : 'Connect Pera Wallet'}
        </button>
      )}
    </div>
  );
}

