'use client';

import { useState } from 'react';
import { getWalletConnect } from '@/lib/sdk';

interface Platform {
  id: string;
  name: string;
  category: 'video' | 'audio' | 'learn';
  icon: string;
  description: string;
  connected: boolean;
  apiKey?: string;
}

const samplePlatforms: Platform[] = [
  {
    id: 'netflix',
    name: 'Netflix',
    category: 'video',
    icon: 'üé¨',
    description: 'Stream movies and TV shows',
    connected: false,
  },
  {
    id: 'spotify',
    name: 'Spotify',
    category: 'audio',
    icon: 'üéµ',
    description: 'Listen to music and podcasts',
    connected: false,
  },
  {
    id: 'coursera',
    name: 'Coursera',
    category: 'learn',
    icon: 'üìö',
    description: 'Online courses and learning',
    connected: false,
  },
  {
    id: 'youtube',
    name: 'YouTube',
    category: 'video',
    icon: 'üì∫',
    description: 'Watch videos and tutorials',
    connected: false,
  },
  {
    id: 'audible',
    name: 'Audible',
    category: 'audio',
    icon: 'üéß',
    description: 'Audiobooks and audio content',
    connected: false,
  },
  {
    id: 'udemy',
    name: 'Udemy',
    category: 'learn',
    icon: 'üéì',
    description: 'Online courses and training',
    connected: false,
  },
];

interface PlatformListProps {
  category: 'video' | 'audio' | 'learn';
}

export default function PlatformList({ category }: PlatformListProps) {
  const [platforms, setPlatforms] = useState<Platform[]>(
    samplePlatforms.filter((p) => p.category === category)
  );
  const [showConnectModal, setShowConnectModal] = useState(false);
  const [selectedPlatform, setSelectedPlatform] = useState<Platform | null>(null);
  const [showRequestModal, setShowRequestModal] = useState(false);

  async function handleConnect(platform: Platform) {
    const wc = await getWalletConnect();
    if (!wc.isWalletConnected()) {
      alert('Please connect your wallet first!');
      return;
    }

    setSelectedPlatform(platform);
    setShowConnectModal(true);
  }

  async function handleDisconnect(platformId: string) {
    // TODO: Implement disconnect logic
    setPlatforms((prev) =>
      prev.map((p) => (p.id === platformId ? { ...p, connected: false, apiKey: undefined } : p))
    );
    alert('Platform disconnected successfully!');
  }

  function handleRequestAccess() {
    setShowRequestModal(true);
  }

  return (
    <div className="space-y-4">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h2 className="text-2xl font-semibold text-zinc-900 dark:text-zinc-100">
          {category === 'video' ? 'Video Platforms' : category === 'audio' ? 'Audio Platforms' : 'Learning Platforms'}
        </h2>
        <button
          onClick={handleRequestAccess}
          className="rounded-lg border border-blue-500 bg-white px-4 py-2 text-sm font-medium text-blue-600 transition-colors hover:bg-blue-50 dark:border-blue-400 dark:bg-zinc-800 dark:text-blue-400 dark:hover:bg-zinc-700"
        >
          + Request New Platform
        </button>
      </div>

      {/* Platform Grid */}
      <div className="grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-3">
        {platforms.map((platform) => (
          <div
            key={platform.id}
            className="rounded-lg border border-zinc-200 bg-white p-6 shadow-sm transition-shadow hover:shadow-md dark:border-zinc-800 dark:bg-zinc-900"
          >
            <div className="mb-4 flex items-center gap-4">
              <div className="text-4xl">{platform.icon}</div>
              <div className="flex-1">
                <h3 className="text-lg font-semibold text-zinc-900 dark:text-zinc-100">
                  {platform.name}
                </h3>
                <p className="text-sm text-zinc-600 dark:text-zinc-400">
                  {platform.description}
                </p>
              </div>
            </div>

            <div className="mb-4">
              {platform.connected ? (
                <div className="rounded-lg bg-green-50 p-3 dark:bg-green-900/20">
                  <div className="mb-2 flex items-center justify-between">
                    <span className="text-sm font-medium text-green-800 dark:text-green-200">
                      Connected
                    </span>
                    <span className="text-green-600">‚úì</span>
                  </div>
                  {platform.apiKey && (
                    <div className="text-xs text-green-700 dark:text-green-300">
                      API Key: {platform.apiKey.slice(0, 8)}...
                    </div>
                  )}
                </div>
              ) : (
                <div className="rounded-lg bg-zinc-50 p-3 dark:bg-zinc-800">
                  <span className="text-sm text-zinc-600 dark:text-zinc-400">Not Connected</span>
                </div>
              )}
            </div>

            <div className="flex gap-2">
              {platform.connected ? (
                <button
                  onClick={() => handleDisconnect(platform.id)}
                  className="flex-1 rounded-lg border border-red-300 bg-white px-4 py-2 text-sm font-medium text-red-600 transition-colors hover:bg-red-50 dark:border-red-700 dark:bg-zinc-800 dark:text-red-400 dark:hover:bg-zinc-700"
                >
                  Disconnect
                </button>
              ) : (
                <button
                  onClick={() => handleConnect(platform)}
                  className="flex-1 rounded-lg bg-blue-500 px-4 py-2 text-sm font-medium text-white transition-colors hover:bg-blue-600"
                >
                  Connect Wallet
                </button>
              )}
            </div>
          </div>
        ))}
      </div>

      {/* Connect Modal */}
      {showConnectModal && selectedPlatform && (
        <ConnectPlatformModal
          platform={selectedPlatform}
          onClose={() => {
            setShowConnectModal(false);
            setSelectedPlatform(null);
          }}
          onSuccess={(apiKey) => {
            setPlatforms((prev) =>
              prev.map((p) =>
                p.id === selectedPlatform.id ? { ...p, connected: true, apiKey } : p
              )
            );
            setShowConnectModal(false);
            setSelectedPlatform(null);
          }}
        />
      )}

      {/* Request Access Modal */}
      {showRequestModal && (
        <RequestPlatformModal
          onClose={() => setShowRequestModal(false)}
          onSuccess={() => {
            setShowRequestModal(false);
            alert('Request submitted! We will review and contact you soon.');
          }}
        />
      )}
    </div>
  );
}

// Connect Platform Modal
function ConnectPlatformModal({
  platform,
  onClose,
  onSuccess,
}: {
  platform: Platform;
  onClose: () => void;
  onSuccess: (apiKey: string) => void;
}) {
  const [apiKey, setApiKey] = useState('');
  const [loading, setLoading] = useState(false);

  async function handleConnect() {
    setLoading(true);
    try {
      // TODO: Implement actual connection logic
      // For now, simulate API key generation
      const generatedKey = `pk_${Math.random().toString(36).substring(2, 15)}`;
      await new Promise((resolve) => setTimeout(resolve, 1000));
      onSuccess(generatedKey);
    } catch (err) {
      alert('Connection failed. Please try again.');
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4">
      <div className="w-full max-w-md rounded-lg border border-zinc-200 bg-white p-6 shadow-lg dark:border-zinc-800 dark:bg-zinc-900">
        <div className="mb-4 flex items-center justify-between">
          <h3 className="text-xl font-semibold text-zinc-900 dark:text-zinc-100">
            Connect to {platform.name}
          </h3>
          <button
            onClick={onClose}
            className="text-zinc-400 hover:text-zinc-600 dark:hover:text-zinc-300"
          >
            ‚úï
          </button>
        </div>

        <div className="mb-4">
          <label className="mb-2 block text-sm font-medium text-zinc-900 dark:text-zinc-100">
            API Key (Optional)
          </label>
          <input
            type="text"
            value={apiKey}
            onChange={(e) => setApiKey(e.target.value)}
            placeholder="Enter your API key or leave empty for auto-connect"
            className="w-full rounded-lg border border-zinc-300 bg-white px-4 py-2 text-zinc-900 placeholder-zinc-500 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-500/20 dark:border-zinc-700 dark:bg-zinc-800 dark:text-zinc-100"
          />
          <p className="mt-1 text-xs text-zinc-600 dark:text-zinc-400">
            If you have an API key, enter it. Otherwise, we'll generate one for you.
          </p>
        </div>

        <div className="flex gap-2">
          <button
            onClick={onClose}
            className="flex-1 rounded-lg border border-zinc-300 bg-white px-4 py-2 text-sm font-medium text-zinc-900 transition-colors hover:bg-zinc-50 dark:border-zinc-700 dark:bg-zinc-800 dark:text-zinc-100"
          >
            Cancel
          </button>
          <button
            onClick={handleConnect}
            disabled={loading}
            className="flex-1 rounded-lg bg-blue-500 px-4 py-2 text-sm font-medium text-white transition-colors hover:bg-blue-600 disabled:opacity-50"
          >
            {loading ? 'Connecting...' : 'Connect'}
          </button>
        </div>
      </div>
    </div>
  );
}

// Request Platform Modal
function RequestPlatformModal({
  onClose,
  onSuccess,
}: {
  onClose: () => void;
  onSuccess: () => void;
}) {
  const [formData, setFormData] = useState({
    platformName: '',
    platformUrl: '',
    category: 'video' as 'video' | 'audio' | 'learn',
    description: '',
    requestType: 'introduce' as 'introduce' | 'api' | 'developer',
    email: '',
  });
  const [loading, setLoading] = useState(false);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    try {
      // TODO: Implement actual submission logic
      await new Promise((resolve) => setTimeout(resolve, 1000));
      onSuccess();
    } catch (err) {
      alert('Submission failed. Please try again.');
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4">
      <div className="w-full max-w-2xl rounded-lg border border-zinc-200 bg-white p-6 shadow-lg dark:border-zinc-800 dark:bg-zinc-900">
        <div className="mb-4 flex items-center justify-between">
          <h3 className="text-xl font-semibold text-zinc-900 dark:text-zinc-100">
            Request Platform Access
          </h3>
          <button
            onClick={onClose}
            className="text-zinc-400 hover:text-zinc-600 dark:hover:text-zinc-300"
          >
            ‚úï
          </button>
        </div>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="mb-2 block text-sm font-medium text-zinc-900 dark:text-zinc-100">
              Request Type
            </label>
            <select
              value={formData.requestType}
              onChange={(e) =>
                setFormData({ ...formData, requestType: e.target.value as any })
              }
              className="w-full rounded-lg border border-zinc-300 bg-white px-4 py-2 text-zinc-900 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-500/20 dark:border-zinc-700 dark:bg-zinc-800 dark:text-zinc-100"
            >
              <option value="introduce">Introduce Our Service to Platform</option>
              <option value="api">Request API Access</option>
              <option value="developer">Request Developer Account</option>
            </select>
          </div>

          <div>
            <label className="mb-2 block text-sm font-medium text-zinc-900 dark:text-zinc-100">
              Platform Name
            </label>
            <input
              type="text"
              value={formData.platformName}
              onChange={(e) => setFormData({ ...formData, platformName: e.target.value })}
              placeholder="e.g., Netflix, Spotify"
              required
              className="w-full rounded-lg border border-zinc-300 bg-white px-4 py-2 text-zinc-900 placeholder-zinc-500 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-500/20 dark:border-zinc-700 dark:bg-zinc-800 dark:text-zinc-100"
            />
          </div>

          <div>
            <label className="mb-2 block text-sm font-medium text-zinc-900 dark:text-zinc-100">
              Platform URL
            </label>
            <input
              type="url"
              value={formData.platformUrl}
              onChange={(e) => setFormData({ ...formData, platformUrl: e.target.value })}
              placeholder="https://..."
              required
              className="w-full rounded-lg border border-zinc-300 bg-white px-4 py-2 text-zinc-900 placeholder-zinc-500 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-500/20 dark:border-zinc-700 dark:bg-zinc-800 dark:text-zinc-100"
            />
          </div>

          <div>
            <label className="mb-2 block text-sm font-medium text-zinc-900 dark:text-zinc-100">
              Category
            </label>
            <select
              value={formData.category}
              onChange={(e) => setFormData({ ...formData, category: e.target.value as any })}
              className="w-full rounded-lg border border-zinc-300 bg-white px-4 py-2 text-zinc-900 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-500/20 dark:border-zinc-700 dark:bg-zinc-800 dark:text-zinc-100"
            >
              <option value="video">Video</option>
              <option value="audio">Audio</option>
              <option value="learn">Learning</option>
            </select>
          </div>

          <div>
            <label className="mb-2 block text-sm font-medium text-zinc-900 dark:text-zinc-100">
              Description
            </label>
            <textarea
              value={formData.description}
              onChange={(e) => setFormData({ ...formData, description: e.target.value })}
              placeholder="Describe the platform and why you want to connect..."
              rows={3}
              required
              className="w-full rounded-lg border border-zinc-300 bg-white px-4 py-2 text-zinc-900 placeholder-zinc-500 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-500/20 dark:border-zinc-700 dark:bg-zinc-800 dark:text-zinc-100"
            />
          </div>

          <div>
            <label className="mb-2 block text-sm font-medium text-zinc-900 dark:text-zinc-100">
              Your Email
            </label>
            <input
              type="email"
              value={formData.email}
              onChange={(e) => setFormData({ ...formData, email: e.target.value })}
              placeholder="your@email.com"
              required
              className="w-full rounded-lg border border-zinc-300 bg-white px-4 py-2 text-zinc-900 placeholder-zinc-500 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-500/20 dark:border-zinc-700 dark:bg-zinc-800 dark:text-zinc-100"
            />
          </div>

          <div className="flex gap-2">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 rounded-lg border border-zinc-300 bg-white px-4 py-2 text-sm font-medium text-zinc-900 transition-colors hover:bg-zinc-50 dark:border-zinc-700 dark:bg-zinc-800 dark:text-zinc-100"
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={loading}
              className="flex-1 rounded-lg bg-blue-500 px-4 py-2 text-sm font-medium text-white transition-colors hover:bg-blue-600 disabled:opacity-50"
            >
              {loading ? 'Submitting...' : 'Submit Request'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

