'use client';

import { usePathname } from 'next/navigation';
import Link from 'next/link';
import { Suspense } from 'react';

const tabs = [
  { id: 'watch', label: 'Watch', icon: 'üì∫', href: '/?tab=watch' },
  { id: 'listen', label: 'Listen', icon: 'üéµ', href: '/?tab=listen' },
  { id: 'learn', label: 'Learn', icon: 'üìö', href: '/?tab=learn' },
  { id: 'wallet', label: 'Wallet', icon: 'üí∞', href: '/wallet' },
];

function NavigationTabsContent() {
  const pathname = usePathname();
  
  // Get active tab from URL on client side
  const getActiveTab = () => {
    if (typeof window !== 'undefined') {
      const params = new URLSearchParams(window.location.search);
      return params.get('tab') || 'watch';
    }
    return 'watch';
  };

  const isActive = (tabId: string) => {
    if (tabId === 'wallet') {
      return pathname.startsWith('/wallet');
    }
    return getActiveTab() === tabId;
  };

  return (
    <div className="flex items-center gap-1 border-b border-zinc-200 dark:border-zinc-800">
      {tabs.map((tab) => {
        const active = isActive(tab.id);
        return (
          <Link
            key={tab.id}
            href={tab.href}
            className={`flex items-center gap-2 px-4 py-3 text-sm font-medium transition-colors ${
              active
                ? 'border-b-2 border-blue-500 text-blue-600 dark:text-blue-400'
                : 'text-zinc-600 hover:text-zinc-900 dark:text-zinc-400 dark:hover:text-zinc-100'
            }`}
          >
            <span>{tab.icon}</span>
            <span>{tab.label}</span>
          </Link>
        );
      })}
    </div>
  );
}

export default function NavigationTabs() {
  return (
    <Suspense fallback={
      <div className="flex items-center gap-1 border-b border-zinc-200 dark:border-zinc-800">
        {tabs.map((tab) => (
          <div key={tab.id} className="px-4 py-3 text-sm text-zinc-400">
            {tab.icon} {tab.label}
          </div>
        ))}
      </div>
    }>
      <NavigationTabsContent />
    </Suspense>
  );
}

